.postsBlock {
    padding: 10px;
}

.posts {
    margin-top: 10px;
}

.textarea {
    border: none;
    outline: none;
    cursor: pointer;
    font: inherit;
    padding: 2px 5px;
    /* margin: 2px 4px; */
    border-radius: 7px;
    background-image: linear-gradient(to right, #afe8df 0%, #8498db 51%, #9EEFE1 100%);
    font-size: 14px;
    color: white;
    box-shadow: 0 16px 32px 2px rgba(0, 40, 120, .35);
    transition: .5s;
}

.button {
    border: none;
    cursor: pointer;
    display: inline-block;
    color: white;
    padding: 3px 6px;
    border-radius: 10px;
    letter-spacing: 1px;
    background-image: linear-gradient(to right, #80e3ea 0%, #26537c 51%, #62aacd 100%);
    background-size: 200% auto;
    box-shadow: 0 0 20px rgba(0, 0, 0, .1);
    transition: .5s;

}

.button:hover {
    background-position: right center;
    transform: scale(1.02);
}